<template>
	<div class="wrapper">
		<mu-form :model="form" class="mu-demo-form" label-position="top" label-width="200">
			<mu-form-item prop="input" label="Name of Room">
				<mu-text-field v-model="form.name"></mu-text-field>
			</mu-form-item>

			<mu-form-item prop="checkbox" label="Ships">
				<mu-checkbox v-model="form.ships" value="AircraftCarrier" label="Aircraft Carrier"></mu-checkbox>
				<mu-checkbox v-model="form.ships" value="Battleship" label="Battleship"></mu-checkbox>
				<mu-checkbox v-model="form.ships" value="Cruiser" label="Cruiser"></mu-checkbox>
				<mu-checkbox v-model="form.ships" value="Destroyer" label="Destroyer"></mu-checkbox>
				<mu-checkbox v-model="form.ships" value="Submarine" label="Submarine"></mu-checkbox>
			</mu-form-item>
			<mu-form-item v-for="(item,idx) in form.ships" :key="idx" prop="input" :label="`Num of ${item}`">
				<mu-text-field v-model="form.shipNums[item]" placeholder="int"></mu-text-field>
			</mu-form-item>
			<mu-form-item prop="input" :label="`Size of Grid:${form.gridSize}×${form.gridSize}`">
				<mu-text-field v-model="form.gridSize" placeholder=">=10,<=20,int"></mu-text-field>
			</mu-form-item>
			<mu-form-item>
				<mu-button color="primary" @click="go">Go</mu-button>
			</mu-form-item>
		</mu-form>
	</div>
</template>
<script>
export default {
	data() {
		return {
			form: {
				name: 'FIGHTING!!',
				gridSize: 10,
				ships: [
					'AircraftCarrier',
					'Battleship',
					'Cruiser',
					'Destroyer',
					'Submarine',
				],
				shipNums: {
					'AircraftCarrier': 1,
					'Battleship': 1,
					'Cruiser': 1,
					'Destroyer': 2,
					'Submarine': 2,
				},
			}
		}
	},
	methods: {
		go() {
			sessionStorage.setItem('config', JSON.stringify(this.form))
			this.$http({
				url: '/rooms',
				method: 'post',
				data: {
					...this.form,
					homeowner: sessionStorage.getItem('user') || 'zys'
				}
			}).then(id => {
				sessionStorage.setItem('currRoom', id)
				this.$router.push({
					path: '/game',
					query: { id, role: 'homeowner' }
				})
			})

		}
	}
}
</script>
<style lang="less" scoped>
.wrapper {
	width: 700px;
	margin: 30px auto;
}
</style>


